body {
  margin: 0;
}

.objective-section {
  background: radial-gradient(rgba(255, 51, 51, 0.5), rgb(0, 0, 0));
}

.title {
  font-size: 100px;
  font-family: "Lato", sans-serif;
  font-weight: 500;
  line-height: 100px;
  color: #fff;
  text-align: center;
  margin: 0;
  padding-top: 92px;
}

.subtitle {
  width: 470px;
  font-size: 22px;
  font-family: "Lato", sans-serif;
  font-weight: 400;
  line-height: 28px;
  text-align: center;
  color: #fff;
  margin: 0 auto;
  margin-top: 20px;
  padding-bottom: 60px;
}

.objective__card-container {
  width: 71.15%;
  margin: 0 auto;
  padding-bottom: 100px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
}

.objective-card {
  width: 23.26%;
  height: 280px;
  background: linear-gradient(to top left, rgba(47, 46, 46, 0.5), rgba(204, 204, 204, 0.5));
  border: 1px solid black;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.objective-card img {
  margin-top: 30px;
}

.objective__card-text {
  width: 88.1%;
  font-size: 22px;
  font-family: "Lato", sans-serif;
  font-weight: 400;
  line-height: 29px;
  color: #fff;
  text-align: center;
  margin-top: 20px;
}

.text-section {
  background: rgb(0, 0, 0);
  position: relative;
}

.text-container {
  width: 51.5%;
  height: 730px;
  display: flex;
  align-items: center;
  margin: 0 auto;
  text-align: center;
}

.text {
  color: #fff;
  margin-top: 0;
  font-size: 50px;
  font-family: "Lato", sans-serif;
  font-weight: 400;
  line-height: 65px;
}

.circle {
  position: absolute;
  bottom: 34.2%;
  left: 20.75%;
  width: 665px;
  height: 278px;
  border: 0.5px solid #FF3333;
  border-radius: 50%;
  transform: rotate(16deg);
}

@media (max-width: 1505px) {
  .objective__card-text {
    font-size: 18px;
  }
}
@media (max-width: 1231px) {
  .objective__card-text {
    font-size: 16px;
  }
}
@media (max-width: 1094px) {
  .circle {
    width: 530px;
    height: 220px;
    bottom: 40%;
  }
  .objective__card-container {
    width: 90%;
  }
  .objective__card-text {
    font-size: 16px;
  }
}
@media (max-width: 957px) {
  .circle {
    width: 500px;
    height: 200px;
    bottom: 40%;
  }
}
@media (max-width: 865px) {
  .objective__card-text {
    font-size: 14px;
  }
  .circle {
    left: 18%;
  }
}
@media (max-width: 768px) {
  .objective__card-container {
    flex-wrap: wrap;
  }
  .objective-card {
    width: 48%;
  }
  .objective-card:nth-child(3) {
    margin-top: 20px;
  }
  .objective-card:nth-child(4) {
    margin-top: 20px;
  }
  .objective__card-text {
    font-size: 22px;
  }
  .circle {
    left: 15%;
  }
}
@media (max-width: 670px) {
  .circle {
    left: 12%;
  }
}
@media (max-width: 616px) {
  .objective__card-text {
    font-size: 20px;
  }
  .circle {
    left: 8%;
  }
  .text {
    font-size: 40px;
  }
}
@media (max-width: 585px) {
  .title {
    font-size: 80px;
  }
  .subtitle {
    font-size: 18px;
  }
  .objective__card-text {
    font-size: 18px;
  }
  .circle {
    width: 420px;
    height: 180px;
  }
  .text {
    font-size: 35px;
  }
}
@media (max-width: 509px) {
  .title {
    font-size: 70px;
  }
  .subtitle {
    font-size: 16px;
  }
  .objective__card-text {
    font-size: 16px;
  }
  .circle {
    left: 6%;
  }
}
@media (max-width: 475px) {
  .circle {
    width: 380px;
    height: 150px;
    left: 4%;
    bottom: 45%;
  }
  .subtitle {
    width: 400px;
  }
  .objective__card-text {
    font-size: 14px;
  }
  .text-container {
    width: 81.5%;
  }
}
@media (max-width: 415px) {
  .circle {
    width: 300px;
    height: 110px;
    left: 4%;
    bottom: 45%;
  }
  .text-container {
    width: 71.5%;
  }
  .text {
    font-size: 35px;
  }
}